@charset "utf-8";
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:400,700');

/**
 * Style variables
 */
$base-font-family:     'Source Code Pro', monospace !default;
$base-font-size:       12.5px !default;
$mobile-font-size:     12px !default;
$base-line-height:     1.5 !default;
$container-width:      90% !default;
$container-max-width:  600px !default;


/**
 * Global
 */
body { 
    background-color: var(--background-color);  
    margin: 0 auto;  
    padding: 0;  
    font-family: $base-font-family;  
    font-size: $base-font-size;  
    color: var(--text-color);  
    text-align: left;  
}
h1, h2, h3, h4, h5, h6 { 
    font-size: $base-font-size;  
    margin: 0px;  margin-top: 22px;  
    font-weight: bold;  
    color: var(--text-color); 
}
p, ul, ol { 
    margin: 0px;  
    color: var(--text-color); 
}
a { 
    text-decoration: underline;  
    color: var(--link-color); 
}
a:hover { 
    color: var(--background-color); 
    background-color: var(--base-color); 
}
img {
    max-width: 48em !important;
}

/* Media queries */
/* smallest */ 
@media (max-width:299px)  { 
    body {
        font-size: $mobile-font-size !important;
    }
    img { 
        max-width: 16em !important ;
    }
}
/* smallererer */ 
@media (min-width:300px)  { 
    body {
        font-size: $mobile-font-size !important;
    }
    img { 
        max-width: 20em !important ;
    }
}
/* smallerer */ 
@media (min-width:320px)  { 
    body {
        font-size: $mobile-font-size !important;
    }
    img { 
        max-width: 23em !important ;
    }
}

/* smaller */ 
@media (min-width:336px)  { 
    body {
        font-size: $mobile-font-size !important;
    }
    img { 
        max-width: 26em !important ;
    }
}

/* smartphones, iPhone, portrait 480x320 phones */ 
@media (min-width:368px)  { 
    body {
        font-size: $mobile-font-size !important;
    }
    img { 
        max-width: 28em !important ;
    }
}

/* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */
@media (min-width:481px)  { 

    img { 
        max-width: 36em !important; 
    }
}

/* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
@media (min-width:641px)  { 
    img { 
        max-width: 48em !important; 
    } 
 }

